import React from "react";
import '../css/Cart.css';

const EmptyCart = ({showCart, setShowCart}) => {

    const handleClick = () => {
        setShowCart(!showCart);
    }

    return (
        <div className="empty-cart">
            <img src="https://www.migros.com.tr/assets/images/cart-empty/cart-empty.webp"
                 alt="empty cart"/>
            <p>Alveri Sepetiniz Bo</p>
            <button onClick={handleClick} className="submit-btn">Alverie Balayn</button>
        </div>
    )
}


export default EmptyCart;